body {
  margin: 0;
  font-family: Helvetica;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ccf5ff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  color: #00a3cc;
  font-size: 30px;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 30px;
}

p {
  text-align: center;
  font-size: 22px;
  color: 	#606060;
}

.flag-picker-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}
.flag-container {
  margin: 36px auto; 
  padding-left: 30px;
  padding-right: 30px;
  width: 300px;
}
.flag-container button {
  background: #1ac6ff;
  border: 0;
  height: 30px;
  width: 120px;
  font-size: 12px;
  border-radius: .25rem;
  box-sizing: border-box;
  color: #fff;
  font-size: 18px;
  text-align: center;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}
.flag-header {
  text-align: center;
}
.continent-option {
  text-align: center;
  height: 25px;
  padding-top: 10px;
  vertical-align: middle;
  margin: auto;
}

.continent-option:hover {
  background-color: #00a3cc;
}


.country-option {
  height: 25px;
  padding-top: 10px;
}

.result {
  text-align: center;
}


