div.searchBox {
    margin: 36px auto; 
    padding-left: 30px;
    padding-right: 30px;
    width: 300px;
   /* float: left;*/
}



.searchBox input[type="text"] {
    width: 100%;
    height: 36px;
    font-size: 18px;
    padding: 10px;
    margin-top: 15px;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box; /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */
    display: block;
    font-weight: 400;
    line-height: 1.6;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.searchBox h2, h3 {
    text-align: center;
}

.searchBox:hover ul {
    display: block;
  }

.searchBox ul {
    display: none;
    list-style-type: none;
    background-color: #fff;
    border-radius: 0 0 5px 5px;
    border: 1px solid #add8e6;
    border-top: none;
    max-height: 180px;
    margin: 0;
    overflow-x: hidden;
    padding: 0;
}

.searchBox ul li {
    /*padding: 7px 9px;*/
    border-bottom: 1px solid #e1e1e1;
    cursor: pointer;
    display: block;
    color: #6e6e6e;
    text-align: left;
    margin: auto;
}

.searchBox ul li.selected {
    background-color: #e8e8e8;
    color: #333;
}

.searchBox-header {
    text-align: center;
}

.DropDown {
    /*padding: 7px 9px;*/
   /* border-bottom: 1px solid #e1e1e1;*/
    /*cursor: pointer;*/
    display: block;
    color: #6e6e6e;
    text-align: left;
}
.DropDown .FlagList {
    height: 36px;
    width: 100%;
    margin-top: 15px;
    font-size: 14px;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box; /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */
    display: block;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;   
}